import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';      // 'actions', './actions'
import mutations from './mutations';
/*import getters from 'getters';
import modules from 'modules';*/

/*
** 1 store
** 2 store(commit) mutations store!!!!
**     eg: store.commit('inrement');
**
*/
Vue.use(Vuex);

const state = {
	loginStatus            :   '',      //
	loginUserInfo          :   '',      //
	balance                :   '',      //
	proxy                  :   '',      //
	gameWins               :   {},      //
	jackpotGames           :   [],      //
	registerConfig         :   '',      //
	collectedGames         :   [],      //
	platformsInfo          :   '',      //

	trueNameStatus         :   '',      //
	userPhoneStatus        :   '',      //
	userEmailStatus        :   '',      //
	userBankCards          :   '',      //

	sportUrl               :   '',      //url
	sportTryUrl            :   '',      //url
	phoneBettingUrl        :   '',      //url
	phoneBettingTryUrl     :   '',      //url

	proxyLoginUrl          :   '',      //url
	onlineServiceUrl       :   '',      //url
	gameCollections        :   [],      //

	showLoginDialog        :   false,
	showRegisterDialog     :   false,
	showAlert              :   false,

	showBack               :   false,

	slotPlatform           :   '',     //
	slotCategoryId         :   '',     //categoryId
	slotCategoryIndex      :   0,      //category
	slotCategoryList       :   [],     //cateGory,


	showSideBar            :   false
};

export default new Vuex.Store({
	state      :  state,
	actions    :  actions,
	mutations  :  mutations
});




